  import urls from '../../../utils/urls.js'
  import sharePop from '../../components/sharePop.vue'
  export default {
    components: {
      sharePop
    },
    data () {
      return {
        showShare: false,
        qrcode: '',
        coin: 0,
		isLogin: this.$store.state.isLogin
      }
    },
    onLoad() {
      // let uid = uni.getStorageSync('user').id
			// let uid = JSON.parse(uni.getStorageSync('userInfo')).id
			let id = '';
			if(this.isLogin){
				id = JSON.parse(uni.getStorageSync('userInfo')).id
			}else{
				 id = 0
			}
      uni.request({
        url: urls.HTTP + '/Generalize/getGeneralize',
        method: 'POST',
        data: {
          uid: id
        },
        success: (res) => {
          // console.log(res)
          this.qrcode = res.data.qrcode
          this.coin = res.data.bonus
        }
      })
    },
    onShareAppMessage(res) {
      return {
        title: '点书吧',
        path: '/pages/common/login'
      }
    },
    methods: {
      share: function () {
        this.showShare = true
        var pages = getCurrentPages()
        var page = pages[pages.length - 1]
        var bitmap = null
        var currentWebview = page.$getAppWebview()
        bitmap = new plus.nativeObj.Bitmap('amway_img')
        // 将web内容绘制到Bitmap对象中
        currentWebview.draw(bitmap, function () {
          bitmap.save("_doc/a.jpg", {}, function (i) {
            uni.saveImageToPhotosAlbum({
              filePath: i.target,
              success:function(){
                bitmap.clear()
                uni.showToast({
                  title: '保存图片成功',
                  mask: false,
                  duration: 1500
                })
              }
            })
          }
          ,function (e) {
            // console.log('保存失败', + JSON.stringify(e))
          })
        },
        function (e) {
          // console.log('截屏失败', JSON.stringify(e))
        })
      },
      closepop: function (val) {
        this.showShare = false
      },
      capture () {
        var pages = getCurrentPages()
        var page = pages[pages.length - 1]
        var bitmap = null
        var currentWebview = page.$getAppWebview()
        bitmap = new plus.nativeObj.Bitmap('amway_img')
        // 将web内容绘制到Bitmap对象中
        currentWebview.draw(bitmap, function () {
          bitmap.save("_doc/a.jpg", {}, function (i) {
            console.log('保存成功：' + JSON.stringify(i))
            uni.saveImageToPhotosAlbum({
              filePath: i.target,
              success:function(){
                console.log(i.target)
                bitmap.clear()
                uni.showToast({
                  title: '保存图片成功',
                  mask: false,
                  duration: 1500
                })
              }
            })
          }
          ,function (e) {
            console.log('保存失败', + JSON.stringify(e))
          })
        },
        function (e) {
          console.log('截屏失败', JSON.stringify(e))
        })
        
      }
    },
    computed: {
      userInfo () {
        return this.$store.state.userInfo
      },
      urlHttp () {
        return urls.UrlHttp
      }
    }
  }
